import React, { Component } from 'react';
import { withRouter } from 'react-router';

// styles images and icons
import { Row, Col } from 'react-bootstrap';
import {
    TextField, InputLabel, FormControl, Select,
    Button
} from '@material-ui/core';
import pp from '../../images/intstractor-profile/pp.png'

// language for test
import text from '../../languages/english/translate.json';

class workExperienceForm extends Component {
    constructor(props) {
        super(props);
        this.state = {
            errors: {
                description: ''
            }
        }
    }
    render() {
        let data = text["instractor-profile"].create.workExperience;

        const handleChange = props => event => {
            const value = event.target.value;
            this.setState({ ...this.state, [props]: event.target.value });

            let errors = this.state.errors;
            switch (props) {
                case 'description':
                    errors.description =
                     value.length <= 5000
                            ? ''
                            : text.validation.description;
                    break;
                default:
                    break;
            }
        }
        const validateForm = (errors) => {
            let valid = true;
            Object.values(errors).forEach(
                // if we have an error string set valid to false
                (val) => val.length > 0 && (valid = false)
            );
            return valid;
        }
        const handleSubmit = (event) => {
            event.preventDefault();
            validateForm(this.state.errors)
        }
        console.log(this.state)
        return (
            <div className="shadow border">
                <div className="border-bottom p-4 text-dark">
                    <Row className="">
                        <Col>
                            <h4>{data.title}</h4>
                            <p className="mb-0">{data.description}</p>
                        </Col>
                    </Row>
                </div>
                <div className="p-4">
                    <div className=" pb-3 text-lef">
                        <form>
                            {/* brief */}
                            <Row className="m-0 mb-2 w-100">
                                <Col className="p-0">
                                    <InputLabel>
                                        {data.briefInput.name}
                                    </InputLabel>
                                    <TextField
                                        multiline
                                        helperText={data.briefInput.validationText}
                                        rows={4}
                                        fullWidth
                                        size="small"
                                        variant="outlined"
                                        onChange={handleChange('description')}
                                    />
                                    <div className="text-danger"><p className="mb-1">{this.state.errors.description}</p></div>
                                </Col>
                            </Row>
                            {/* Years of experience */}
                            <Row className="m-0 mb-0 w-100">
                                <Col className="pr-0 pl-1">
                                    <InputLabel >
                                            {data.yearsOfExperience}
                                    </InputLabel>
                                    <FormControl
                                        className="mb-3"
                                        variant="outlined" fullWidth size="small">
                                        <Select
                                            native
                                            name={data.yearsOfExperience}
                                            onChange={handleChange('yearsOfExperience')}
            
                                        >
                                            <option value=""></option>
                                            <option value="1">1</option>
                                            <option value="2">2</option>
                                        </Select>
                                    </FormControl>
                                </Col>
                            </Row>
                            {/* Uploads*/}
                            <Row className="m-0 mb-3">
                                <Col md={12} className="p-0  mb-2">
                                    <p className="mb-1">{data.uploadText}</p>
                                    <label 
                                        className="mb-0 pt-1 pb-1 pr-3 pl-3 btn-green" 
                                        style={{ cursor: 'pointer' }} 
                                        for="uploadText">
                                        {data.browseBtn}
                                    </label>
                                    <input
                                        onChange={e => {
                                            // make sure that the user attache file
                                            if (e.target.files[0]) {
                                                this.setState({ ...this.state, text: e.target.files[0] })
                                            }
                                        }}
                                        type="file"
                                        accept='image/*'
                                        className="d-none"
                                        id="uploadText" 
                                    />
                                </Col>
                                <Col md={12} className="p-0">
                                    <p className="mb-1">{data.uploadVideo}</p>
                                    <label 
                                            className="mb-0 pt-1 pb-1 pr-3 pl-3 btn-green" 
                                            style={{ cursor: 'pointer' }} 
                                            for="uploadVideo">
                                            {data.browseBtn}
                                    </label>
                                    <input
                                        onChange={e => {
                                            // make sure that the user attache file
                                            if (e.target.files[0]) {
                                                this.setState({ ...this.state, video: e.target.files[0] })
                                            }
                                        }}
                                        type="file"
                                        accept='image/*'
                                        className="d-none"
                                        id="uploadVideo" 
                                    />
                                </Col>
                            </Row>
                            {/* form errors */}
                            <div className="text-danger"><p className="mb-1">{this.state.errors.form}</p></div>
                            {/* back and continue buttons */}
                            <Row className="m-0 align-items-center w-100 border-top pt-3">
                                <Col className="text-left p-0">
                                    <Button
                                        onClick={e=>{
                                            window.scrollTo({top : "0" , behavior : "smooth"})
                                            this.props.history.push({
                                                pathname : "/basicInformation"
                                            })
                                        }}
                                        className="btn-green-outline pl-4 pr-4">
                                        {data.backBtn}
                                    </Button>
                                </Col>
                                <Col className="text-right p-0">
                                    <Button
                                        onClick={handleSubmit}
                                        className="btn-green pl-4 pr-4">
                                        {data.continueBtn}
                                    </Button>
                                </Col>
                            </Row>
                        </form>
                    </div>
                </div>
            </div>
        )
    }
}

export default withRouter(workExperienceForm)
