import React, { Component } from 'react';
import { withRouter } from 'react-router';

// styles images and icons
import { Row, Col } from 'react-bootstrap';
import {
    TextField, InputLabel, FormControl, Select,
    Button
} from '@material-ui/core';
import pp from '../../images/intstractor-profile/pp.png'

// language for test
import text from '../../languages/english/translate.json';

class basicInformationComponent extends Component {
    constructor(props) {
        super(props);
        this.state = {
            errors: {
                firstName: '',
                lastName: '',
                email: ''
            }
        }
    }
    render() {
        let data = text["instractor-profile"].create.basicInfo;
        let dataForm = text.registration

        const validEmailRegex = RegExp(/^(([^<>()\[\]\.,;:\s@\"]+(\.[^<>()\[\]\.,;:\s@\"]+)*)|(\".+\"))@(([^<>()[\]\.,;:\s@\"]+\.)+[^<>()[\]\.,;:\s@\"]{2,})$/i);
        const validPassword = RegExp(/(?=.*\d)(?=.*[a-z])(?=.*[A-Z]).{8,}/);

        const handleChange = props => event => {
            const value = event.target.value;
            this.setState({ ...this.state, [props]: event.target.value });

            let errors = this.state.errors;
            let gender = this.state.gender;
            errors.form = ''
            errors.loginSucc = ''
            switch (props) {
                case 'firstName':
                    errors.firstName =
                        value.length >= 3 && value.length < 191
                            ? ''
                            : text.validation.name;
                    break;
                case 'lastName':
                    errors.lastName =
                        value.length >= 3 && value.length < 191
                            ? ''
                            : text.validation.name;
                    break;
                case 'email':
                    errors.email =
                        validEmailRegex.test(value)
                            ? ''
                            : text.validation.email;
                    break;
                case 'password':
                    errors.password =
                        validPassword.test(value)
                            ? ''
                            : text.validation.password;
                    break;
                case 'confirmPassword':
                    errors.confirmPassword =
                        validPassword.test(value)
                            ? ''
                            : text.validation.password;
                    break;
                case 'gender':
                    gender =
                        value === "true"
                            ? true
                            : false;
                    this.setState({ ...this.state, gender: gender })
                    break;
                default:
                    break;
            }
        }
        const validateForm = (errors) => {
            let valid = true;
            Object.values(errors).forEach(
                // if we have an error string set valid to false
                (val) => val.length > 0 && (valid = false)
            );
            return valid;
        }
        const handleSubmit = (event) => {
            event.preventDefault();
            if(validateForm(this.state.errors)){
                window.scrollTo({ top: 0, behavior: 'smooth' })
                this.props.history.push({
                    pathname : "/workExperience"
                })
            }
        }
        return (
            <div className="shadow border">
                <div className="border-bottom p-4 text-dark">
                    <Row className="">
                        <Col>
                            <h4>{data.title}</h4>
                            <p>{data.description}</p>
                        </Col>
                    </Row>
                </div>
                <div className="p-4">
                    <Row className="align-items-center">
                        <Col md={3} className="">
                            <div className="">
                                <img 
                                    src={this.state.profile ? URL.createObjectURL(this.state.profile) : pp} 
                                    alt="profile" className="img-fluid rounded-circle" />
                            </div>
                        </Col>
                        <Col md={9}>
                            <div>
                                <h5 className="mb-0">{data.profileTitle}</h5>
                                <p className="text-muted">{data.profileDesc}</p>
                            </div>
                            <div className="mt-4">
                                <label 
                                    className="mb-0 pt-1 pb-1 pr-3 pl-3 btn-green" 
                                    style={{ cursor: 'pointer' }} 
                                    for="uploadProfile">
                                    {data.uploadBtn}
                                </label>
                                <input
                                    onChange={e => {
                                        // make sure that the user attache file
                                        if (e.target.files[0]) {
                                            this.setState({ ...this.state, profile: e.target.files[0] })
                                        }
                                    }}
                                    type="file"
                                    accept='image/*'
                                    className="d-none"
                                    id="uploadProfile" 
                                />
                            </div>
                        </Col>
                    </Row>
                    <div className="mt-4 pb-3 text-lef">
                        <form onSubmit={handleSubmit}>
                            {/* name */}
                            <Row className="m-0 mb-2 w-100">
                                {/* first name */}
                                <Col className="pr-1 pl-0 t">
                                    <InputLabel required>
                                        {dataForm.firstName}
                                    </InputLabel>
                                    <TextField
                                        fullWidth
                                        required
                                        className=""
                                        size="small"
                                        variant="outlined"
                                        value={this.state.firstName}
                                        onChange={handleChange('firstName')}
                                    />
                                    <div className="text-danger"><p className="mb-1">{this.state.errors.firstName}</p></div>
                                </Col>
                                {/* last name */}
                                <Col className="pr-0 pl-1 ">
                                    <InputLabel required>
                                        {dataForm.lastName}
                                    </InputLabel>
                                    <TextField
                                        fullWidth
                                        required
                                        className=""
                                        size="small"
                                        variant="outlined"
                                        inputProps={{
                                            value: this.state.lastName
                                        }}
                                        onChange={handleChange('lastName')}
                                    />
                                    <div className="text-danger"><p className="mb-1">{this.state.errors.lastName}</p></div>
                                </Col>
                            </Row>
                            {/* email */}
                            <Row className="m-0 mb-2">
                                 <Col className="pr-0 pl-0">
                                    <InputLabel required>
                                            {dataForm.email}
                                    </InputLabel>
                                    <TextField
                                        size="small"
                                        fullWidth
                                        type="email"
                                        required
                                        onChange={handleChange('email')}
                                        inputProps={{
                                            value: this.state.email
                                        }}
                                        variant="outlined"
                                    />
                                    <div className="text-danger"><p className="mb-1">{this.state.errors.email}</p></div>
                            </Col>                           
                            </Row>
                            {/* gender and data of birth */}
                            <Row className="m-0 mb-2 w-100">
                                {/* data of birth */}
                                <Col className="pr-1 pl-0">
                                    <div className="">
                                        <InputLabel className="">{dataForm.dateOfBirth}</InputLabel>
                                        <TextField
                                            fullWidth
                                            size="small"
                                            type="date"
                                            variant="outlined"
                                            onChange={handleChange('dateOfBirth')}
                                        />
                                    </div>
                                </Col>
                                {/* gender */}
                                <Col className="pr-0 pl-1">
                                    <InputLabel >
                                            {dataForm.gender}
                                    </InputLabel>
                                    <FormControl
                                        className="mb-3"
                                        variant="outlined" fullWidth size="small">
                                        <Select
                                            native
                                            name={dataForm.gender}
                                            onChange={handleChange('gender')}
                                            inputProps={{
                                                name: dataForm.gender,
                                                id: 'outlined-age-native-simple',
                                            }}
                                        >
                                            <option value=""></option>
                                            <option value={true}>Male</option>
                                            <option value={false}>Female</option>
                                        </Select>
                                    </FormControl>
                                </Col>
                            </Row>
                            {/* form errors */}
                            <div className="text-danger"><p className="mb-1">{this.state.errors.form}</p></div>
                            {/* continue button */}
                            <div className="m-0 align-items-center w-100 border-top pt-3">
                                <div className="text-right ">
                                    <Button
                                        type="submit"
                                        className="btn-green pl-3 pr-3">
                                        {data.continueBtn}
                                    </Button>
                                </div>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        )
    }
}

export default withRouter(basicInformationComponent)
